.admin_container {
    width: 100%;
    height: 100vh;
    display: flex;
}
.admin_nav {
    width: 20%;
    display: flex;
    justify-content: space-around;

    flex-direction: column;
}
.admin_main_content {
    width: 80%;
    overflow: auto;
}


/* ADMIN NAVIGATION */

.admin_top_container {
    height: 5%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.navItem {
    height: 10%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-top: solid 1px black;
    border-bottom: solid 1px black;
    
}




/*  ADMIN CONTENT MAIN CONTAINER  */

.admin_content_main_container {
    width: 100%;
    height: 100%;

}

.admin_content_part_one {
    width: 100%;
    height: 30%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.admin_content_part_two {
    width: 100%;
    height: 30%;

    display: flex;
    justify-content: center;
    align-items: center;
}

.admin_content_part_three {
    width: 100%;
    height: 40%;
    display: flex;
    justify-content: center;
    align-items: center
}

.admin_content_part_four {
    width: 100%;
    height: 20%;
}



