@media (min-width: 481px) and (max-width: 767px) {
    .nav_links {
        font-size: 14px;
    }
    .admin_nav_main_title {
        font-size: 18px;
    }
}

/* 
##Device = Most of the Smartphones Mobiles (Portrait)
##Screen = B/w 320px to 479px
*/
        /* BORDER RED */
@media (min-width: 320px) and (max-width: 480px) {

    .icon {
        display: none;
    }
    .navItem {
        font-size: 12px;
    }
    .admin_nav_main_title {
        font-size: 16px;
        margin-left: 10px;
    }
    .dropDown {
        font-size: 12px !important;
    }

}