.cart_container {
    width: 80%;
    height: 70%;
    margin-top: -180px;
}

.cart_title {
    text-align:center;
    font-family: "Raleway-Regular" !important;
}

.shopping_cart {

    height: 88%;
    overflow: auto;
}


/*CART ITEM CSS*/

.shopping_cart_item {
    width: 100%;
    height: 10%;
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.shopping_cart_image {
    width: 20%;
    height: 100%;
    background-color: red;
}

.shopping_cart_stock_div {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}

.shopping_cart_stock_div small {
    margin-top: -10px;
    color: green;
}





            /* RESPONSIVE DESIGN */

    /* BORDER BLUE */
    @media (min-width: 1281px) {

 


    }
    
    /* 
    ##Device = Laptops, Desktops
    ##Screen = B/w 1025px to 1280px
    */
    
        /* BORDER ORANGE */
@media (min-width: 1025px) and (max-width: 1280px) {


    .top_panel_container {
        border: solid blue;
    }

}

/* 
##Device = Tablets, Ipads (portrait)
##Screen = B/w 768px to 1024px
*/


    /* BORDER PURPLE  */
@media (min-width: 768px) and (max-width: 1024px) {


}

/* 
##Device = Tablets, Ipads (landscape)
##Screen = B/w 768px to 1024px
*/

                /* THIS IS THE ONE I'M ON NOW */

    /* BORDER GREEN  */
@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

    
    
    .shopping_cart_stock_div p {
       margin-right: 10px;
    }
    .shopping_cart_stock_div small {
        
        display: none;
    }
    .select_input_cart {
        width: 50px;
        margin-right: 10px;
        margin-left: 10px;
    }
    .remove_button_cart {
        font-size: 12px;
    }
}

/* 
##Device = Low Resolution Tablets, Mobiles (Landscape)
##Screen = B/w 481px to 767px
*/

        /* BORDER YELLOW  */
@media (min-width: 481px) and (max-width: 767px) {

    .cart_title {
        font-size: 18px;
    }
    .cart_container {
        position: relative;
        top: 45px;
        height: 60%;
    }
    .shopping_cart_item {
        height: 40%;
        margin-top: 10px;
    }
    .shopping_cart_image {
        display: none;
    }
    .shopping_cart_stock_div {
        margin-right: 10px;
    }
    .item_price {
        margin-right: 10px;
    }
    .select_input_cart {
        margin-right: 5px;
    }
    .remove_button_cart {
        font-size: 12px;
    }

}

/* 
##Device = Most of the Smartphones Mobiles (Portrait)
##Screen = B/w 320px to 479px
*/
        /* BORDER RED */
@media (min-width: 320px) and (max-width: 480px) {

    .cart_container {
        position: relative;
    }
    .cart_title {
        font-size: 20px;
    }
    .shopping_cart_stock_div p {
        font-size: 12px;
        position: relative;

    }
    .shopping_cart_stock_div small {
        display: none;
    }
    .item_price {
        font-size: 12px;
    }
    .select_input_cart {
        width: 30px;
        height: 20px;
    }
    .shopping_cart_image {
        display: none;
    }
    .shopping_cart_item {
        justify-content: center;
        height: 35%;
        align-items: center;
        flex-direction: column;
    }
    .remove_button_cart {
        font-size: 10px;
    }
}