/* top panel container */
.top_panel_container {
    height: 40%;
}



.top_panel_container div p a {
    color: #A8763E;
    font-family: "Raleway-Regular" !important;
}

.top_panel_container div p a:hover {
    text-decoration: none;
    color: #6F1A07;

}

.top_panel_background_image {
    width: 100%;
    height: 90%;
    background-image: url("../../images/grocery_store_main_image.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;   
}

/* left side grocery nav bar */
.top_panel_nav_container {
    width: 100%;
    height: 10%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.top_panel_nav_container a {
    text-decoration: none;
}

/* grocery items */
.grocery_items_container {
    height: 59%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 30px;
    overflow: auto;
}



