.form_outer_container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.form_inner_container {
    width: 60%;
    height: 60%;
}

.top_form_container {
    height: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.form_container {

    height: 80%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}
.submit_button_container {
    height: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.add_product_all_inputs {
    width: 300px;
    padding: 5px;
    text-align: center;
}



