.card_container {
    height: 400px;
    width: 300px;
    position: relative;
}
.card_container:hover .card_image {
    height: 80%;
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;

}

.card_image {
    width: 100%;
    background-color: blue;
    background-size: cover;
    background-position: center;
    height: 100%;
    transition: 0.3s ease-in-out;
    position: relative;
    z-index: 1111;
}

/* Main div outlining the each product description */
.product_description_div {
    position: absolute;
    top: 80%;
    width: 100%;
    height: 20%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.product_description_div div:nth-child(1) p {
    font-size: 16px;
    font-family: 'Cantarell', sans-serif;
    
}

.product_description_div div:nth-child(2) p {
    font-size: 16px;
    font-family: 'Raleway-Regular', sans-serif;
}

.product_description_div div:nth-child(3) button {
    outline: none;
    border:none;
    padding: 10px;
    border-radius: 10%;
    transition: 0.3s ease-in-out;
    font-family: 'Raleway-Regular', sans-serif;
    background-color: white;
    color: #A8763E;
    border: 1px solid #A8763E;
    margin-top: -10px;
}

.product_description_div div:nth-child(3) button:hover {
    color: #6F1A07;
    border: solid 1px #6F1A07;
}



