/* 
##Device = Tablets, Ipads (landscape)
##Screen = B/w 768px to 1024px
*/

                /* THIS IS THE ONE I'M ON NOW */

    /* BORDER GREEN  */
@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

    .card_container {
        height: 350px;
        width: 250px;
    }

    .product_description_div div:nth-child(1) p {
        font-size: 14px;
    }

    .product_description_div div:nth-child(2) p {
        font-size: 14px;
    }

    .product_description_div div:nth-child(3) button {
        font-size: 14px;
        padding: 8px;
    }

}

/* 
##Device = Low Resolution Tablets, Mobiles (Landscape)
##Screen = B/w 481px to 767px
*/

        /* BORDER YELLOW  */
@media (min-width: 481px) and (max-width: 767px) {

    .card_container {
        height: 300px;
        width: 180px;
    }
    .product_description_div {
        justify-content: space-around;
    }
    .product_description_div div:nth-child(1) p {
        font-size: 14px !important;
    }
    .product_description_div div:nth-child(2) p {
        font-size: 14px !important;
        margin-left: 5px;
    }
    .product_description_div div:nth-child(3) button {
        margin-left: 5px;
        padding: 1px !important;
        margin-top: 0px;
        position: relative;
        bottom: 8px;
        font-size: 14px !important;
    }
}

/* 
##Device = Most of the Smartphones Mobiles (Portrait)
##Screen = B/w 320px to 479px
*/
        /* BORDER RED */
@media (min-width: 320px) and (max-width: 480px) {


    .card_container {
        height: 250px;
        width: 200px;
    }
    .card_image {
        height: 80%;

    }
    .card_container:hover .card_image {
    
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
}
    .product_description_div div:nth-child(1) p {
        font-size: 12px;
        
    }
    
    .product_description_div div:nth-child(2) p {
        font-size: 12px;
       
    }
    .product_description_div div:nth-child(3) button {
        font-size: 12px;
        padding: 5px;
        position: relative;
        bottom: 5px;
    }

}