.admin_card_container {

    height: 80%;
    width: 30%;
    border-radius: 5%;
    box-shadow: 5px 5px 0px 7px;
}

.admin_main_content_all_top_div p {
    padding-top: 10px;
    padding-left: 10px;
}


.div1_main_content {
    height: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.div1_main_content p:nth-child(1) {
    font-weight: 800;
    font-size: 35px;
}

.div2_main_content {
    height: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.div2_main_content p:nth-child(1) {
    font-weight: 800;
    font-size: 35px;
}

.div3_main_content {
    height: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.div3_main_content p:nth-child(1) {
    font-weight: 800;
    font-size: 35px;
}


/*SECOND SECTION OF MAIN ADMIN CONTENT*/
.second_part_div1_title p {
    text-align: center;
    padding: 10px;
    font-size: 40px;
}

.second_part_div1_main_content {
    height: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

}
.second_part_div1_main_content p:nth-child(1) {
    font-weight: 800;
    font-size: 35px;
}





