@media (min-width: 500px) and (max-width: 546px) {
    body {
        border: solid green;
    }
    .div1_main_content p:nth-child(1) {
        font-size: 18px;
     }
     .div2_main_content p:nth-child(1) {
        font-size: 18px;
     }
     .div3_main_content p:nth-child(1) {
        font-size: 18px;
     }
  
     .second_part_div1_title p {
        font-size: 22px;
    }
}

        /* BORDER YELLOW  */
@media (min-width: 481px) and (max-width: 767px) {
   .div1_title p {
       font-size: 16px;
   }

   .div1_main_content p:nth-child(1) {
      font-size: 28px;
   }

   .div2_title p {
    font-size: 16px;
   }

   .div2_main_content p:nth-child(1) {
       font-size: 28px;
   }

   .div3_title p:nth-child(1) {
       font-size: 16px;
   }
   .div3_main_content p:nth-child(1) {
       font-size: 28px;
   }

   .second_part_div1_title p {
       font-size: 28px;
   }
   .inventory_inventory {
       display: none;
   }
}

/* 
##Device = Most of the Smartphones Mobiles (Portrait)
##Screen = B/w 320px to 479px
*/
        /* BORDER RED */
@media (min-width: 320px) and (max-width: 480px) {
    .div1_title {
        font-size: 16px;
    }
    .admin_main_content_all_top_div p {
        padding-top: 0px;
    }

    .div1_main_content p {
        font-size: 14px;
    }
    .div1_main_content p:nth-child(1) {
        position: relative;
        bottom: 10px;

    }

    /*Div 2 Section*/
    .div2_title {
        font-size: 16px;
    }
    .div2_main_content p {
        font-size: 14px;
    }
    .div2_main_content p:nth-child(2) {
        position: relative;
        left: 10px;
        bottom: 5px;
    }

    /*DIV 3 Section*/
    .div3_main_content p {
        font-size: 14px;
    }
    .div3_title {
        font-size: 16px;
    }
    .div3_main_content p:nth-child(2) {
        position: relative;
        left: 10px;
        bottom: 5px;
    }

    /*SECOND PART SECTION*/

    .second_part_div1_title p {
        font-size: 25px;
    }
    .second_part_div1_main_content p:nth-child(1) {
        font-size: 25px;
    }

    .second_part_div1_main_content p:nth-child(2) {
        font-size: 16px;
    }

    .inventoryTopMainContainer {
        margin-left: 2px;
    }
    .inventoryMainCheeseTop {
        position: relative;
        left: 10px;
    }
    .inventoryStockCheese {
        font-size: 12px;
        position: relative;
        left: 15px;
    }
    .inventoryMainCheeseLength {
        position: relative;
        left: 10px;
        top: 0spx;
    }
    .inventory_inventory {
        display: none;
    }

}