.admin_page_login_container {
    border: solid blue;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.admin_login_main_div {
    width: 80%;
    height: 30%;
    border: solid 1px rgb(90, 90, 90);
}

.admin_login_top_panel {
    height: 20%;
    width: 100%;
}
.admin_login_top_panel h4 {
    padding: 20px;
    font-family: "Notable-Regular" !important;
}

.admin_login_second_half {
    height: 68%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction:  column;
}
.admin_login_second_half div:nth-child(1) {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
}

.admin_login_second_half div:nth-child(1) label {
    font-family: "Raleway-Regular" !important;
}

.admin_login_second_half div:nth-child(1) input {
    font-family: "Raleway-Regular" !important;
}

.admin_login_second_half div:nth-child(2) {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
}

.admin_login_second_half div:nth-child(2) input {
    font-family: "Raleway-Regular" !important;
}

.admin_login_second_half div:nth-child(2) label {
    font-family: "Raleway-Regular" !important;
}

.admin_login_second_half div:nth-child(3) {
    padding: 5px;
    margin-bottom: 15px;
    font-family: "Raleway-Regular" !important;
}

.admin_login_second_half div:nth-child(3) button {
    border: none;
    outline: none;
    background-color: white;
    
}

.admin_login_second_half input {
    margin: 10px;
    width: 300px;
    padding: 10px;
    outline: none;
}

.admin_login_bottom_links {
    font-family: "Raleway-Regular" !important;
}



/* RESPONSIVE DESIGN */

.admin_login_top_panel h4 {
    font-size: 16px;
}

.admin_login_second_half div:nth-child(1) input {
    width: 150px;
    font-size: 12px;
}
.admin_login_second_half div:nth-child(2) input {
    width: 150px;
    font-size: 12px;
}

.admin_login_second_half div:nth-child(1) label {
    font-size: 12px;
}
.admin_login_second_half div:nth-child(2) label {
    font-size: 12px;
}


.admin_login_second_half div:nth-child(3) button {
    font-size: 12px;
}
.admin_login_links_container {
    margin-top: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

        /* PLACEHOLDER TEXT STYLING*/
.admin_login_second_half div:nth-child(2) input::-moz-placeholder {
   font-size: 12px;
}
.admin_login_second_half div:nth-child(2) input::-webkit-input-placeholder {
    font-size: 12px;
}
.admin_login_second_half div:nth-child(2) input::placeholder {
    font-size: 12px;
}

.admin_login_second_half div:nth-child(1) input::-moz-placeholder {
    font-size: 12px;
 }
 .admin_login_second_half div:nth-child(1) input::-webkit-input-placeholder {
     font-size: 12px;
 }
 .admin_login_second_half div:nth-child(1) input::placeholder {
     font-size: 12px;
 }